import React, { useEffect, useState } from "react";
import { Button, ButtonGroup, Container, OverlayTrigger, Tooltip } from "react-bootstrap";
import { MdOutlineDeleteOutline, MdOutlineEdit } from 'react-icons/md';
import { useDispatch } from "react-redux";
import { useHistory } from "react-router";
import { Col, Row } from "reactstrap";
import Switch from "../../components/Switch";
import TableCustom from "../../components/Table";
import { app } from "../../firebase_config";
import { formatUsers } from "../../helpers/dtos/users";
import { setValue } from "../../store/ducks/users/actions";
import DeleteForm from "./components/DeleteForm";
import HeaderList from "./components/HeaderList";

let fetchDataDebounced = null;

function Report({ history }) {
	const dispatch = useDispatch();
	const nav = useHistory();
	// const { data, loading } = useSelector((state) => state.users)
	const [state, setState] = useState([])
	const [status, setStatus] = useState(false)
	const [loading, setLoading] = useState(false)
	const columns =
		[
			{
				Header: "Nome",
				accessor: "fullName"
			},
			{
				Header: "Email",
				id: "email",
				sortable: true,
				accessor: "email"
			},
			{
				Header: "Telefone",
				sortable: false,
				accessor: "phone"
			},
			{
				Header: "Perfil",
				id: "profile",
				sortable: false,
				accessor: "profile"
			},
			{
				Header: 'Aes',
				cell: (client) => {
					return (
						<div style={{display: 'flex', flexDirection:'row', gap: 8, alignItems: 'center'}}>
						<ButtonGroup>
							<Button
								variant="secondary"

								onClick={() => handleEdit(client)}
								className="d-flex font-weight-bold text-white btn btn-xs"
							>
								<MdOutlineEdit className="text-white font-weight-bold mr-1" />
								<span className="text-white font-weight-bold mr-1">
									Editar
								</span>
							</Button>
							
							<Button
								variant="warning"
								onClick={() => handleDelete(client)}
								className="d-flex text-black font-weight-bold btn btn-xs"
							>
								<MdOutlineDeleteOutline className="text-black font-weight-bold mr-1" />
								<span className="text-black font-weight-bold mr-1">
									Excluir
								</span>
							</Button>
						</ButtonGroup>

							{client.profileId === '9421e035-1fd6-4918-8fc7-f4e865996bbc' && (
								<div style={{display: 'flex', flexDirection:'column'}}>
									<OverlayTrigger
									placement='top'
									overlay={
										<Tooltip id={`tooltip-produtora`}>
										{`${client.status==2?'Desativar':'Ativar'}`} <strong>produtora</strong>.
										</Tooltip>
									}
									>
									<Button style={{ background: 'transparent',
														border: 'transparent',
														height: '5px',
														width: '100%',
														padding: 0,
														margin:0}}></Button>

									</OverlayTrigger>
									<Switch
									onChange={(e)=>updateUser(client, 'status', e)}
									defaultValue={client.status==2? true: client.status==0 && false}
									/>
								</div>
							)}
						</div>

					);
				},
			},
		]


	useEffect(() => {

		getUsers()
	}, [])
	const getUsers = async () => {
		setLoading(true)
		try {
			const res = await app.firestore().collection('users').get()
			let users = [];
			res.forEach(user => {
				users.push({ id: user.id, ...user.data() })
			});
			const user = formatUsers(users)
			setState(user)
			setLoading(false)


		} catch (err) {
			setLoading(false)
			console.log(err);
		}
	};

	const updateUser = (userDetails, param, value) => {
		delete userDetails.password
		// if(value && userDetails.status!==1) {return}else{
			const convertStatus = value? 2: 0;
			userDetails = {...userDetails, [param]: convertStatus}
			  app.firestore().collection('users').doc(userDetails.id)
				.set(userDetails,{merge: true})
				.then(res=>console.log("Document successfully written!"))
				.catch(error => {
				  console.log(error);
				  setLoading(false)
				})
			  setLoading(false)
			  getUsers();
		// }

	
	  };

	const handleDelete = (user) => {
		dispatch(setValue({ showModalDelete: true, userSelected: user }));
	}


	const handleEdit = (user) => {

		dispatch(setValue({ userSelected: user }));
		nav.push(`/users/edit/${user.id}`)
	}

	return (
		<div className="dashboard">
			<Row>
				<Col md={12} className="table-responsive col-md-12">
					<HeaderList />

					<TableCustom
						title="Usurios"
						data={state}
						loading={loading}
						columns={columns}
					/>
				</Col>
			</Row>
			<DeleteForm />
		</div>

	);
}

export default Report;