import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory, useParams } from 'react-router-dom';
import { app } from '../../firebase_config';
import profiles from '../../helpers/models/profiles';
import { dateFormat } from '../../helpers/utils';
import { loadCategoryShow } from '../../store/ducks/shows/actions';
import { useToast } from '../../_contexts/toast';
import EditForm from './components/EditForm';

function EditUsers() {
  const dispatch = useDispatch();
  const [userSelected, setUserSelected] = useState(null)
  const [error, setError] = useState([])
  const [loading, setLoading] = useState(false)
  const { showToast } = useToast();
  const [profileSelected, setProfileSelected] = useState(null);
  const [producerBankData, setProducerBankData] = useState([{
    bank: "",
    bankBranch: "",
    account: "",
    pixType: "",
    pixNumber: "",
  }]);
  const { categories } = useSelector((state) => state.shows);
  const history = useHistory();
  const { id } = useParams();

  useEffect(() => {
    dispatch(loadCategoryShow())
  }, [])

  useEffect(() => {
    getData()
  }, [])

  const getData = ()=>{
    console.log('Aqui');
    app.firestore().collection("users").doc(id).get()
    .then(res => {
      const user = res.data();
    console.log('Aqui', user);

      setUserSelected(user)
      if(user.profileId==='9421e035-1fd6-4918-8fc7-f4e865996bbc'){
        setProducer(user);
        setProfile(user.profileId)
      }
      
    });
  }

  const onSubmitLogin = async data => {
    // const errors = validate(data);

    // if (Object.keys(errors).length > 0) {
    // 	throw new SubmissionError(errors);
    // }
    const values = {
      birthday: data.birthday,
      email: data.email,
      fullName: data.fullName,
      password: data.password,
      phone: data.phone,
      profileId: data.profile?.value
    }
    signupUserLogin(values)

  };

  const onSubmitPessoal = async data => {
    // const errors = validate(data);

    // if (Object.keys(errors).length > 0) {
    // 	throw new SubmissionError(errors);
    // }
    const values = {
      birthday: data.birthday,
      email: data.email,
      fullName: data.fullName,
      password: data.password,
      phone: data.phone,
      profileId: data.profile?.value
    }
    signupUserPessoais(values)

  };


  const onSubmitPerfil = async data => {
    // const errors = validate(data);

    // if (Object.keys(errors).length > 0) {
    // 	throw new SubmissionError(errors);
    // }
    const values = {
      profileId: data.profile?.value
    }
    signupUserPerfil(values)

  };

  const signupUserLogin = async (userDetails) => {
    setLoading(true)
    const { fullName, profileId, birthday, email, password } = userDetails;
    const user = app.auth().currentUser;
    try {
      await user.updateEmail(email)
      await user.updatePassword(password)
      showToast('success', 'Cadastro efetuado com sucesso');
      setLoading(false)
    } catch (error) {
      console.log(error)
      showToast('error', 'Ooops algo deu errado ')
    }
  };

  const signupUserPessoais = (userDetails) => {
    setLoading(true)
    const { fullName, profileId, birthday, email, password } = userDetails;
    app.firestore().collection('users').doc(app.auth().currentUser.uid)
      .set({
        fullName,
        birthday,
      }, {merge:true}).then(res => {
        setLoading(false)
        showToast('success', 'Cadastro efetuado com sucesso');
      })
      .catch(error => {
        console.log(error);
        setLoading(false)
        setError(error)
        showToast('error', 'Ooops algo deu errado ')

      })


  };

  const signupUserPerfil = (userDetails) => {
    setLoading(true)
    const { profileId } = userDetails;
    app.firestore().collection('users').doc(app.auth().currentUser.uid)
      .set({
        profileId
      }, {merge:true}).then(res => {
        setLoading(false)
        showToast('success', 'Cadastro efetuado com sucesso');
      })
      .catch(error => {
        console.log(error);
        setLoading(false)
        setError(error)
        showToast('error', 'Ooops algo deu errado ')

      })


  };

  const categoriesOptions = categories.length ? categories?.map(category => (
    { value: category.id, label: category.name.toUpperCase() }
  )) : [{ value: null, label: 'Selecione uma categoria' }]

  const profilesOptions = Object.keys(profiles).map(profile => (
    { value: profile, label: profiles[profile] }
  ))

  const handleProfileSelected = (e) => {
    setProfileSelected(e);
  }

  const handleProducerBank = (e, index) => {
    console.log('objeto', e, index);
    const { name, value,id } = e.target;
    const arrayCopy = producerBankData.map((producer, i) => {
      if (i === index) {
        producer[id] = value

      }
      return producer
    })
    console.log('arrayNew', arrayCopy);
  }

  const addProducerBank = () => {
    if(producerBankData.length===5){
      return;
    }
    setProducerBankData([...producerBankData, {
      "bank": "",
      "bankBranch": "",
      "account": "",
      "pixType": "",
      "pixNumber": "",
    }]);
  }

  const setProducer = (user)=>{
    setProducerBankData([...user.producerBank])
  }

  const setProfile = (profileId)=>{
    setProfileSelected(profilesOptions.find(profile=>profile.value===profileId))
  }

  const buildDate = (date)=>{
    return dateFormat(date) ? `${dateFormat(date).year}-${dateFormat(date).month}-${dateFormat(date).day}`: null
  }
  const initialValues = userSelected && {
    ...userSelected,
    birthday: userSelected.birthday && String(userSelected.birthday).search('Z')!==-1 ? buildDate(userSelected.birthday): '',
    fullName: userSelected.fullName,
    phone: userSelected.phone,
    category: categoriesOptions.find(category => category.id === userSelected.category),
    profileId: profilesOptions.find(profile=>profile.value===userSelected.profileId)
    }

  return (
    <div className="dashboard align-items-center px-0 lock-full-bg w-80 h-80">
      <div className="row w-100 mx-0">
        <div className="col-md-12 col-lg-12 mx-auto">
          <div className="container">
            <h1 className="display-4 text-white">Editar Usurio</h1>
          </div>
          <EditForm
            handleSubmitLogin={onSubmitLogin}
            handleSubmitPessoal={onSubmitPessoal}
            handleSubmitPerfil={onSubmitPerfil}
            handleProfileSelected={handleProfileSelected}
            loading={loading}
            profilesOptions={profilesOptions}
            initialValues={initialValues}
            categoriesOptions={categoriesOptions}
            profileSelected={profileSelected}
            handleProducerBank={handleProducerBank}
            producerBankData={producerBankData}
            addProducerBank={addProducerBank}
          />
        </div>
      </div>
    </div>
  );
}

export default EditUsers
