import { set } from 'lodash';
import React, { useEffect, useState } from 'react';
import { Row } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { SubmissionError } from 'redux-form';
import { app } from '../../firebase_config';
import profiles from '../../helpers/models/profiles';
import { convertToNumber, dateFormat } from '../../helpers/utils/index.js';
import { loadCategoryShow } from '../../store/ducks/shows/actions';
import { useToast } from '../../_contexts/toast';
import CreateForm from './components/CreateForm';
import EditProdutoraForm from './components/EditProdutoraForm';
import EditProdutoraStatic from './components/EditProdutoraStatic';
import validate from './components/validate';

function EditProdutora() {
  const dispatch = useDispatch();
  // const { loading, error } = useSelector(s => s.users)
  const [error, setError] = useState([])
  const [loading, setLoading] = useState(false)
  const [userLoggedData, setUserLoggedData] = useState(null)
  const [profileSelected, setProfileSelected] = useState(null);
  const [status, setStatus] = useState(0)
  const [producerBankData, setProducerBankData] = useState([{
    bank: "",
    bankBranch: "",
    account: "",
    pixType: "",
    pixNumber: "",
  }]);
  const { categories } = useSelector((state) => state.shows);
  const { showToast } = useToast();
  const history = useHistory();
  const user = JSON.parse(localStorage.getItem("@PermissionYT:user"));

  const onSubmit = async data => {
    // const errors = validate(data);

    // if (Object.keys(errors).length > 0) {
    // 	throw new SubmissionError(errors);
    // }
    const values = {
      birthday: data.birthday?new Date(data.birthday).toISOString(): '',
      email: 'produtoraOwner@gmail.com' || userLoggedData.email,
      fullName: data.fullName? data.fullName: '',
      password: data.password,
      phone: convertToNumber(data.phone),
      profileId: userLoggedData.profileId,
      category: data.category?.value || null,
      description: data.description?data.description:'',
      thumbnail: data.thumbnail?data.thumbnail:'',
      socialNetwork: {
        instagram: data.rdInstagram?data.rdInstagram:'',
        facebook: data.rdFacebook?data.rdFacebook:'',
        site:data.rdSite?data.rdSite:''
      },
      corporateData:{
        cnpj: convertToNumber(data.cnpj),
        companyName: data.companyName?data.companyName:''
      },
      producerBankData
    }
    signupUser(values)



  };

  const signupUser = (userDetails) => {
    setLoading(true)
        delete userDetails.password
      userDetails = {...userDetails, status: 1}
      console.log(userDetails);
        app.firestore().collection('users').doc(user.id)
          .set(userDetails, {merge: true})
          .then(res=>console.log("Document successfully written!"))
          .catch(error => {
            console.log(error);
            setLoading(false)
            setError(error)
            showToast('error', 'Ooops algo deu errado ')

          })
        setLoading(false)
        getData();
        showToast('success', 'Cadastro efetuado com sucesso');

  };

  const profileType = (profileId) => {
    return profiles[profileId].toUpperCase();
  }

  useEffect(() => {
    dispatch(loadCategoryShow())
  }, [])

  useEffect(() => {
    getData()
  }, [user.id])


  useEffect(() => {
    if(userLoggedData && userLoggedData.producerBank){
      setProducer()
    }
  }, [userLoggedData])


  useEffect(() => {
    if(userLoggedData){
      setProfile()
    }
  }, [userLoggedData])


  useEffect(() => {
    if(userLoggedData){
      setStatus(userLoggedData.status || 0)
    }
  }, [userLoggedData])

  const getData = ()=>{
    app.firestore().collection("users").doc(user.id).get()
    .then(res => {
      setUserLoggedData(res.data())
    });
  }

  const categoriesOptions = categories.length ? categories?.map(category => (
    { value: category.id, label: category.name.toUpperCase() }
  )) : [{ value: null, label: 'Selecione uma categoria' }]

  const profilesOptions = Object.keys(profiles).map(profile => (
    { value: profile, label: profiles[profile] }
  ))

  const handleProfileSelected = (e) => {
    setProfileSelected(e);
  }

  const handleProducerBank = (e, index) => {
    console.log('objeto', e, index);
    const { name, value,id } = e.target;
    const arrayCopy = producerBankData.map((producer, i) => {
      if (i === index) {
        producer[id] = value

      }
      return producer
    })
    console.log('arrayNew', arrayCopy);
  }

  const addProducerBank = () => {
    if(producerBankData.length===5){
      return;
    }
    setProducerBankData([...producerBankData, {
      "bank": "",
      "bankBranch": "",
      "account": "",
      "pixType": "",
      "pixNumber": "",
    }]);
  }
  const setProducer = ()=>{
    setProducerBankData([...userLoggedData.producerBank])
  }

  const setProfile = ()=>{
    setProfileSelected(profilesOptions[1])
  }

  const buildDate = (date)=>{
    return dateFormat(date) ? `${dateFormat(date).year}-${dateFormat(date).month}-${dateFormat(date).day}`: null
  }
  const initialValues = userLoggedData && {
    ...userLoggedData,
    birthday: userLoggedData.birthday && String(userLoggedData.birthday).search('Z')!==-1 ? buildDate(userLoggedData.birthday): '',
    fullName: userLoggedData.fullName,
    phone: userLoggedData.phone,
    category: categoriesOptions.find(category => category.id === userLoggedData.category),
  }


  return (
    <div className="dashboard align-items-center px-0 lock-full-bg w-80 h-80">
      <div className="row w-100 mx-0">
        <div className="col-md-12 col-lg-12 mx-auto">
          <div className="dashboard px-4 mb-2">
            <h1 className="display-4 text-white font-weight-bold">Meus Dados</h1>
          </div>
          {status <= 1?
          (
          <EditProdutoraForm
          onSubmit={onSubmit}
          loading={loading}
          profilesOptions={profilesOptions}
          categoriesOptions={categoriesOptions}
          handleProfileSelected={handleProfileSelected}
          profileSelected={profileSelected}
          handleProducerBank={handleProducerBank}
          producerBankData={producerBankData}
          addProducerBank={addProducerBank}
          initialValues={initialValues}
          />
          ):(
            <EditProdutoraStatic initialValues={initialValues} producerBankData={producerBankData}/>
          )}

        </div>
      </div>
    </div>
  );
}

export default EditProdutora
