import React from 'react';
import { Button, Form, Tab, Tabs } from 'react-bootstrap';
import { BiPlus } from 'react-icons/bi';
import { Link } from 'react-router-dom';
import { Field, reduxForm } from "redux-form";
import InputCustom from '../../../components/Input';
import { CNPJ, DATE, PHONE } from '../../../components/NumberFormat';
import renderSelectField from '../../../components/Select';
import Spinner from '../../../Layout/Spinner';

function EditForm({
  handleSubmitLogin,
  handleSubmitPessoais,
  handleProfileSelected,
  profileSelected,
  reset,
  loading,
  initialValues,
  profilesOptions,
  categoriesOptions,
  handleProducerBank,
  producerBankData,
  addProducerBank
}) {
  console.log(initialValues, 'Valor inicial');
  console.log(profileSelected, 'profile');
  return (
    <div className="row">
      <div className="col-md-12">
        <div className="justify-content-between align-items-center tab-transparent">
          <Tabs defaultActiveKey="login" id="uncontrolled-tab-example" className="nav">
            <Tab eventKey="login" title="Dados de Login" className="test-tab">
              <Form autoComplete="new-off" className="form" onSubmit={handleSubmitLogin} >

                <Form.Row>
                  <Form.Group className="col-md-6">
                    <label for="email">Email</label>
                    <Field
                      id="email"
                      name="email"
                      component={InputCustom}
                      className="form-control"
                      type="text"
                      placeholder="email"
                    />
                  </Form.Group>
                  <Form.Group className="col-md-6">
                    <label for="password">Senha</label>
                    <Field
                      id="password"
                      name="password"
                      component={InputCustom}
                      className="form-control"
                      type="password"
                      placeholder="SENHA"
                    />
                  </Form.Group>
                </Form.Row>

                <div className="row flex-row-reverse px-2">
                  <div className="btn-group align-self-end">
                    <Button className="btn btn-secondary btn-lg font-weight-medium" type="button" onClick={reset} disabled={loading}>
                      {loading ? (
                        <Spinner />
                      ) : (
                        "Limpar"
                      )}
                    </Button>
                    <Button className="btn btn-primary btn-lg font-weight-medium" type="submit" disabled={loading}>
                      {loading ? (
                        <Spinner />
                      ) : (
                        "Cadastrar"
                      )}
                    </Button>
                  </div>
                </div>
              </Form>
            </Tab>
            <Tab eventKey="pessoal" title="Dados Pessoais" className="test-tab">
              <Form autoComplete="new-off" className="pt-3" onSubmit={handleSubmitPessoais} >

                <Form.Row>
                  <Form.Group className="col-md-5">
                    <label for="fullName">Nome</label>
                    <Field
                      id="fullName"
                      name="fullName"
                      component={InputCustom}
                      className="form-control "
                      type="text"
                      placeholder="NOME COMPLETO"
                    />
                  </Form.Group>
                  <Form.Group className="col-md-4">
                    <label for="phone">Telefone</label>
                    <Field
                      id="phone"
                      name="phone"
                      component={PHONE}
                      className="form-control"
                      type="text"
                      placeholder="TELEFONE"
                    />
                  </Form.Group>
                  <Form.Group className="col-md-3">
                    <label for="birthday">Data de Aniversrio</label>
                    <Field
                      id="birthday"
                      name="birthday"
                      component={DATE}
                      className="form-control"
                      type="text"
                      placeholder="DATA DE ANIVERSRIO"
                    />
                  </Form.Group>
                </Form.Row>
                <Form.Row>
                  <Form.Group className="col-md-12">
                    <label for="profile">Perfil</label>
                    <Field
                      id="profile"
                      name="profileId"
                      component={renderSelectField}
                      className="form-control"
                      type="text"
                      placeholder="SELECIONE UM PERFIL"
                      defaultValue={initialValues}
                      options={profilesOptions}
                      disabled={true}
                      // onChange={(e) => handleProfileSelected(e)}
                    />
                  </Form.Group>
                </Form.Row>
                {profileSelected && profileSelected?.label === 'Produtora' ? (
                  <>
                    <div className="card-form pt-5 px-5 mb-5">
                      <Form.Group className="col-md-9">
                        <label>Dados da Produtora</label>
                      </Form.Group>
                    </div>
                    <div className="card-form pt-5 px-5 mb-5">
                      <Form.Row>
                        <Form.Group className="col-md-5">
                          <label for="category">Categoria</label>
                          <Field
                            id="category"
                            name="category"
                            component={renderSelectField}
                            className="form-control"
                            type="text"
                            options={categoriesOptions}
                            placeholder="Selecione uma categoria"
                          />
                        </Form.Group>
                        <Form.Group className="col-md-7">
                          <label for="description">Descrio</label>
                          <Field
                            id="description"
                            name="description"
                            component={InputCustom}
                            as="textarea"
                            rows={3}
                            className="form-control"
                            type="text"
                            placeholder="Descrio da produtora..."
                          />
                        </Form.Group>
                        <Form.Group className="col-md-12">
                          <label for="urlImage">Imagem</label>
                          <Field
                            id="urlImage"
                            name="urlImage"
                            component={InputCustom}
                            className="form-control"
                            type="text"
                            placeholder="URL de Foto ou logomarca da produtora..."
                          />
                        </Form.Group>
                      </Form.Row>

                    </div>
                    <div className="card-form pt-5 px-5 mb-5">
                      <Form.Row>
                        <Form.Group className="col-md-9">
                          <label>Redes Sociais</label>
                        </Form.Group>
                      </Form.Row>
                      <Form.Row>
                        <Form.Group className="col-md-12">
                          <label for="rdInstagram">Instagram</label>
                          <Field
                            id="rdInstagram"
                            name="rdInstagram"
                            component={InputCustom}
                            className="form-control"
                            type="text"
                            placeholder="@produtora"
                          />
                        </Form.Group>
                        <Form.Group className="col-md-12">
                          <label for="rdFacebook">Facebook</label>
                          <Field
                            id="rdFacebook"
                            name="rdFacebook"
                            component={InputCustom}
                            className="form-control"
                            type="text"
                            placeholder="https://www.facebook.com/produtora"
                          />
                        </Form.Group>
                        <Form.Group className="col-md-12">
                          <label for="rdSite">Site</label>
                          <Field
                            id="rdSite"
                            name="rdSite"
                            component={InputCustom}
                            className="form-control"
                            type="text"
                            placeholder="https://www.produtora.com"
                          />
                        </Form.Group>
                      </Form.Row>

                    </div>
                    <div className="card-form pt-5 px-5 mb-5">
                      <Form.Row>
                        <Form.Group className="col-md-9">
                          <label>Dados Bancrios</label>
                        </Form.Group>
                      </Form.Row>
                      {producerBankData.map((producer, index) => (
                        <div className='' key={index}>
                          <Form.Row>
                            <Form.Group className="col-md-3">
                              <label for="bank">Banco</label>
                              <Field
                                id={'bank'}
                                name={'bank' + index}
                                component={InputCustom}
                                className="form-control"
                                type="text"
                                onChange={(e) => handleProducerBank(e, index)}
                              />
                            </Form.Group>
                            <Form.Group className="col-md-4">
                              <label for="bankBranch">Agncia</label>
                              <Field
                                id="bankBranch"
                                name={'bankBranch' + index}
                                component={InputCustom}
                                className="form-control"
                                type="text"
                                onChange={(e) => handleProducerBank(e, index)}
                              />
                            </Form.Group>
                            <Form.Group className="col-md-5">
                              <label for="account">Conta</label>
                              <Field
                                id="account"
                                name={'account' + index}
                                component={InputCustom}
                                className="form-control"
                                type="text"
                                onChange={(e) => handleProducerBank(e, index)}
                              />
                            </Form.Group>
                          </Form.Row>
                          <Form.Row>
                            <Form.Group className="col-md-3">
                              <label for="pixType">Tipo de Pix</label>
                              <Field
                                id="pixType"
                                name={'pixType' + index}
                                component={InputCustom}
                                className="form-control"
                                type="text"
                                onChange={(e) => handleProducerBank(e, index)}
                              />
                            </Form.Group>
                            <Form.Group className="col-md-9">
                              <label for="pixNumber">Chave Pix</label>
                              <Field
                                id="pixNumber"
                                name={'pixNumber' + index}
                                component={InputCustom}
                                className="form-control"
                                type="text"
                                onChange={(e) => handleProducerBank(e, index)}
                              />
                            </Form.Group>
                          </Form.Row>
                        </div>
                      ))}
                      <div className="d-flex justify-content-center mb-3">
                        <Button className="btn btn-primary " style={{ borderRadius: '50%', height: '50px', width: '50px' }} disabled={loading || producerBankData.length === 5} type="button" onClick={addProducerBank}>
                          <BiPlus size={22} />
                        </Button>
                      </div>
                    </div>
                    <div className="card-form pt-5 px-5 mb-5">
                      <Form.Row>
                        <Form.Group className="col-md-5">
                          <label for="cnpj">CNPJ</label>
                          <Field
                            id="cnpj"
                            name="cnpj"
                            component={CNPJ}
                            className="form-control"
                            type="text"
                          />
                        </Form.Group>
                        <Form.Group className="col-md-7">
                          <label for="companyName">Nome Comercial</label>
                          <Field
                            id="companyName"
                            name="companyName"
                            component={InputCustom}
                            className="form-control"
                            type="text"
                          />
                        </Form.Group>
                      </Form.Row>

                    </div>
                  </>
                ) : ''}

                <div className="row flex-row-reverse px-2">
                  <div className="btn-group align-self-end">
                  <Button className="btn btn-secondary btn-lg font-weight-medium" type="button" onClick={reset} disabled={loading}>
                      {loading ? (
                        <Spinner />
                      ) : (
                        "Limpar"
                      )}
                    </Button>
                    <Button className="btn btn-block btn-primary btn-lg font-weight-medium" type="submit" disabled={loading}>
                      {loading ? (
                        <Spinner />
                      ) : (
                        "Cadastrar"
                      )}
                    </Button>
                  </div>
                </div>
              </Form>
            </Tab>
          </Tabs>
        </div>
      </div>
    </div>
  );
}

export default reduxForm({
  form: "form_edit_user",
  // validate
})(EditForm);