import React from 'react';

import { GrFormSearch } from 'react-icons/gr';
import { FiUserPlus } from 'react-icons/fi';
import { Button } from 'react-bootstrap';

function HeaderList() {
    return (
        <div className="container px-20 m-0 mb-5">
            <div className="row justify-content-between w-100 p-0 m-0">

                <div className="col-sm-12 col-md-5">
                    <h2 className="text-white title-header-table">Gerenciamento de Usurios</h2>
                </div>

                <div className="col-sm-12 col-md-4">
                    <div className="input-group h2">
                        <input name="data[search]" className="form-control" id="search" type="text" placeholder="Pesquisar Itens" />
                        <span className="input-group-prepend">
                            <button className="btn btn-primary" type="submit">
                                <span className="glyphicon-search text-white">
                                    <GrFormSearch />
                                </span>
                            </button>
                        </span>
                    </div>
                </div>

                <div className="col-sm-12 col-md-3">
                    <Button className="btn btn-primary w-100 h2 text-nowrap">
                        <FiUserPlus className="text-black mr-2" />
                        <span className="text-black font-weight-bold">Cadastrar Usurio</span>
                    </Button>
                </div>
            </div>
        </div>
    );
}

export default HeaderList;